import { BACKEND_API_URL } from '../Constants/constant';
import { apiRequest } from '../utils/apiRequest';

export const FetchSystemConfig = async () => {
  try {
    return await apiRequest(BACKEND_API_URL.FETCH_SYSTEM_CONFIG, 'GET');
  } catch (err) {
    console.error('Error FetchSystemConfig:', err);
    throw new Error(`${err.message}`);
  }
};