import { BACKEND_API_URL } from '../../Constants/constant';
import { apiRequest } from '../../utils/apiRequest';

export const createCustomer = async (customerData) => {
  try {
    return await apiRequest(BACKEND_API_URL.CUSTOMER_CREATE, 'POST', customerData);
  } catch (error) {
    console.error('Error creating customer:', error);
    throw error;
  }
};

export const updateCustomer = async (customerId, customerData) => {
  try {
    return await apiRequest(BACKEND_API_URL.CUSTOMER_UPDATE(customerId), 'PATCH', customerData);
  } catch (error) {
    console.error('Error updating customer:', error);
    throw error;
  }
};

export const getCustomer = async (chatId) => {
  try {
    return await apiRequest(BACKEND_API_URL.CUSTOMER_GET(chatId), 'GET');
  } catch (error) {
    // Dont do anything
  }
};

export const deleteCustomer = async (customerId) => {
  try {
    return await apiRequest(BACKEND_API_URL.CUSTOMER_DELETE(customerId), 'DELETE');
  } catch (error) {
    console.error('Error deleting customer:', error);
    throw error;
  }
};

export const getCompanies = async (queryParams) => {
  try {
    const { page = 1, page_size = 10, name = '' } = queryParams;
    const urlParams = new URLSearchParams({
      page: page.toString(),
      page_size: page_size.toString(),
      name: name
    });
    
    const url = `${BACKEND_API_URL.CUSTOMER_COMPANIES}?${urlParams}`;
    return await apiRequest(url, 'GET', queryParams);
  } catch (error) {
    console.error('Error fetching companies:', error);
    throw error;
  }
};

const CustomerService = {
  create: createCustomer,
  update: updateCustomer,
  get: getCustomer,
  delete: deleteCustomer,
  getCompanies: getCompanies,
};

export default CustomerService;
