import { DB_CONFIG } from '../../Constants/constant';
import { IndexedDBUtils } from '../../utils/indexedDBUtils';
import chatArtifactsManager from '../../services/ChatArtifacts/chatArtifactsManager';
import chatArtifactStorage from '../../services/ChatArtifacts/chatArtifactsStorage';

/**
 * Base class for background checker services providing common functionality
 */
export class BaseCheckerService {
  constructor(config) {
    this.CONFIG = config;
    this.state = this.createState();
  }

  // Abstract method to be implemented by subclasses
  createState() {
    throw new Error('createState() must be implemented by subclass');
  }

  // Common Data Access Layer
  static async getUserData() {
    try {
      const stores = [
        {
          name: DB_CONFIG.STORES.USER_DATA,
          options: { keyPath: 'id' }
        }
      ];

      const db = await IndexedDBUtils.openDatabase(DB_CONFIG.NAME, DB_CONFIG.VERSION, stores);
      const userData = await IndexedDBUtils.getFromStore(db, DB_CONFIG.STORES.USER_DATA, 'currentUser');
      db.close();

      return userData;
    } catch (error) {
      console.warn('Failed to get user data from IndexedDB:', error);
      return null;
    }
  }

  static async getLoggedUserId() {
    const userData = await this.getUserData();
    return userData?.userId;
  }

  // Common Alarm Management
  setupAlarms() {
    try {
      this.clearExistingAlarms();
      this.createAlarms();
      this.setupAlarmListeners();

      console.log(`${this.getServiceName()} set up successfully`);
    } catch (error) {
      console.error(`Error setting up ${this.getServiceName()} alarms:`, error);
      throw error;
    }
  }

  clearExistingAlarms() {
    Object.values(this.CONFIG.ALARM_NAMES).forEach(alarmName => {
      chrome.alarms.clear(alarmName);
    });
  }

  createAlarms() {
    // To be implemented by subclasses
    throw new Error('createAlarms() must be implemented by subclass');
  }

  setupAlarmListeners() {
    if (!chrome.alarms.onAlarm.hasListener(this.handleAlarm.bind(this))) {
      chrome.alarms.onAlarm.addListener(this.handleAlarm.bind(this));
    }
  }

  handleAlarm(alarm) {
    // To be implemented by subclasses
    throw new Error('handleAlarm() must be implemented by subclass');
  }

  // Common Setup Function Logic
  setup() {
    if (this.state.isSetup) {
      console.log(`${this.getServiceName()} already setup, skipping...`);
      return;
    }

    if (!chrome?.alarms) {
      console.error('Chrome alarms API not available');
      return;
    }

    try {
      this.state.isSetup = true;
      // Trigger an immediate sync when the extension (service worker) reloads
      this.syncLoggedUserData();
      setTimeout(() => {
        this.syncLoggedUserData();
        this.setupAlarms();
      }, 1000);
    } catch (error) {
      console.error(`Error setting up ${this.getServiceName()}:`, error);
      this.state.isSetup = false;
    }
  }

  // Common Sync Service Logic
  async syncLoggedUserData() {
    try {
      const userData = await BaseCheckerService.getUserData();

      if (!userData?.userId) {
        console.log(`No authenticated user found for ${this.getServiceName()} sync`);
        return;
      }

      const userId = userData.userId;

      // Check if sync is already in progress for this user
      if (this.state.activeSyncProcesses.get(userId)) {
        console.log(`${this.getServiceName()} sync already in progress for user ${userId}`);
        return;
      }

      await this.performUserSync(userId);
    } catch (error) {
      console.error(`Error syncing ${this.getServiceName()} for logged user:`, error);
    }
  }

  async performUserSync(userId) {
    this.state.activeSyncProcesses.set(userId, true);

    try {
      console.log(`Starting ${this.getServiceName()} sync for logged user ${userId}`);

      await chatArtifactsManager.initialize({ loggedUserId: userId });

      const data = await this.fetchUserData(userId);

      this.state.lastSyncTimes.set(userId, Date.now());
      console.log(`[SERVER] Fetched ${data?.length || 0} pending ${this.getDataType()} from server at:`, new Date());

      return data;
    } catch (error) {
      console.error(`Error syncing ${this.getServiceName()} for user ${userId}:`, error);
      throw error;
    } finally {
      this.state.activeSyncProcesses.delete(userId);
    }
  }

  async fetchUserData(userId) {
    let page = 1;
    let hasMoreData = true;
    const aggregated = [];

    while (hasMoreData && page <= this.CONFIG.SYNC.MAX_PAGES) {
      try {
        const response = await this.fetchDataPage(userId, page);

        if (!response?.data || !Array.isArray(response.data)) {
          break;
        }

        const data = response.data;
        aggregated.push(...data);

        hasMoreData = !!response.next && data.length === this.CONFIG.SYNC.PAGE_SIZE;
        page++;
      } catch (pageError) {
        console.error(`Error syncing ${this.getServiceName()} page ${page}:`, pageError);
        break;
      }
    }

    return aggregated;
  }

  async fetchDataPage(userId, page) {
    try {
      return await chatArtifactsManager.list({
        data_type: this.getDataType(),
        loggedUserId: userId,
        status: 'pending',
        page: page,
        page_size: this.CONFIG.SYNC.PAGE_SIZE
      });
    } catch (apiError) {
      console.warn(`API request failed for ${this.getServiceName()} page ${page}:`, apiError);
      throw apiError;
    }
  }

  // Common Data Service Logic
  async listAllPendingData(userId) {
    await chatArtifactStorage.initialize(userId);
    const data = await chatArtifactStorage.getChatData(null, this.getDataType(), { status: 'pending' });
    console.log(`[STORAGE] Fetched ${data?.length || 0} pending ${this.getDataType()} from storage at:`, new Date());
    return data || [];
  }

  async updateData(itemId, itemData, chatId, status = 'pending', metaData = {}) {
    const userId = await BaseCheckerService.getLoggedUserId();
    await chatArtifactsManager.update({
      artifact_id: itemId,
      main_content: JSON.stringify(itemData),
      meta_data: metaData,
      data_type: this.getDataType(),
      chatId: chatId,
      loggedUserId: userId,
      status: status
    });
  }

  // Abstract methods to be implemented by subclasses
  getServiceName() {
    throw new Error('getServiceName() must be implemented by subclass');
  }

  getDataType() {
    throw new Error('getDataType() must be implemented by subclass');
  }

  // Common utility methods
  static parseRecord(record) {
    try {
      const content = JSON.parse(record.main_content || '{}');
      return { ...record, ...content };
    } catch (error) {
      console.error(`Invalid main_content for record ${record.id}:`, error);
      return null;
    }
  }

  static getItemKey(item) {
    return item?.apiId || item?.id;
  }

  static getChatId(item) {
    return item?.chatId || item?.chat_id;
  }

  // Cleanup tracking
  cleanupTracking() {
    // To be implemented by subclasses with specific cleanup logic
  }
}