import { BACKEND_API_URL } from '../Constants/constant';
import { apiRequest } from '../utils/apiRequest';

export const SubmitFeedback = async (payload, tabId) => {
  try {
    return await apiRequest(BACKEND_API_URL.SUBMIT_FEEDBACK(payload.messageId), 'POST', payload, tabId);
  } catch (err) {
    console.error('Error SubmitFeedback:', err);
    throw new Error(`${err.message}`);
  }
};