import { BACKEND_API_URL } from '../Constants/constant';
import { apiRequest } from '../utils/apiRequest';

export const UpdateUserConfig = async (payload, tabId) => {
  try {
    return await apiRequest(BACKEND_API_URL.UPDATE_USER_CONFIG, 'PATCH', payload, tabId);
  } catch (err) {
    console.error('Error UpdateUserConfig:', err);
    throw new Error(`${err.message}`);
  }
};
