import { encryptData } from '../utils/EncryptDecrypt';
import { BACKEND_API_URL } from '../Constants/constant'
import { apiRequest } from '../utils/apiRequest';

export const fetchAiReply = async (payload, tabId) => {
  try {
    return await apiRequest(BACKEND_API_URL.FETCH_REPLY, 'POST', payload, tabId);
  } catch (err) {
    console.error('Error fetchAiReply:', err);
    throw new Error(`${err.message}`);
  }
};
