/**
 * Background Script Tracking Helper
 *
 * Sends tracking events from background script to content script running on WhatsApp Web.
 * This avoids Amplitude cookie/localStorage errors in Service Worker context.
 */

/**
 * Send a tracking event from background script to WhatsApp Web content script
 * @param {string} eventName - The event name from TRACK_EVENTS
 * @param {Object} eventProperties - Optional event properties
 */
export async function trackEventFromBackground(eventName, eventProperties = {}) {
  try {
    // Find active WhatsApp Web tabs
    const tabs = await chrome.tabs.query({
      url: '*://web.whatsapp.com/*',
      active: true
    });

    if (tabs.length === 0) {
      // No active WhatsApp tabs, try any WhatsApp tab
      const allTabs = await chrome.tabs.query({ url: '*://web.whatsapp.com/*' });

      if (allTabs.length === 0) {
        console.warn('No WhatsApp Web tabs found for tracking event:', eventName);
        return;
      }

      // Send to first available WhatsApp tab
      await sendTrackingMessage(allTabs[0].id, eventName, eventProperties);
    } else {
      // Send to active WhatsApp tab
      await sendTrackingMessage(tabs[0].id, eventName, eventProperties);
    }
  } catch (error) {
    console.error('Error sending tracking event from background:', error);
  }
}

/**
 * Send tracking message to specific tab
 * @param {number} tabId - Chrome tab ID
 * @param {string} eventName - Event name
 * @param {Object} eventProperties - Event properties
 */
async function sendTrackingMessage(tabId, eventName, eventProperties) {
  try {
    await chrome.tabs.sendMessage(tabId, {
      type: 'TRACK_EVENT_FROM_BACKGROUND',
      payload: {
        eventName,
        properties: eventProperties
      }
    });
  } catch (error) {
    // Tab might not have content script injected yet, or might be unloaded
    console.warn(`Could not send tracking event to tab ${tabId}:`, error.message);
  }
}
