import { encryptData } from '../utils/EncryptDecrypt';
import { BACKEND_API_URL } from '../Constants/constant'
import { apiRequest } from '../utils/apiRequest';

export const FetchUserConfig = async ( payload, tabId ) => {
  try {
    return await apiRequest(BACKEND_API_URL.FETCH_USER_CONFIG, 'GET', payload, tabId);
  } catch (err) {
    console.error('Error FetchUserConfig:', err);
    throw new Error(`${err.message}`);
  }
};
