import * as Services from '../services';
import { handleReminderAction } from './services/ReminderChecker.js';
import { handleScheduledMessageDeliverySuccess, handleScheduledMessageDeliveryFailure } from './services/ScheduledMessageChecker.js';

function handleAsyncMessage(request, sendResponse, asyncHandler) {
  (async () => {
    try {
      const response = await asyncHandler({ ...request.payload, requestType: request.type });
      sendResponse({ success: true, data: response });
    } catch (error) {
      console.error(`Error processing ${request.type}:`, error);
      sendResponse({ success: false, error: error.message });
    }
  })();
  return true;
}

function setupMessageListeners() {
  chrome.runtime.onMessage.addListener((request, sender, sendResponse) => {
    const tabId = sender.tab?.id || null;

    switch (request.type) {
      case 'ONBOARDING_FORM_SUBMIT':
        handleAsyncMessage(request, sendResponse, async (payload) => {
          return await Services.Onboarding(payload);
        });
        return true;
      case 'CALL_USER_LOGIN':
        handleAsyncMessage(request, sendResponse, async (payload) => {
          return await Services.UserLogin(payload.phoneNumber);
        });
        return true;

      case 'FETCH_USER_TOKEN':
        handleAsyncMessage(request, sendResponse, async (payload) => {
          return await Services.GenerateToken(payload.userId, tabId);
        });
        return true;

      case 'GET_USER_CONFIG':
        handleAsyncMessage(request, sendResponse, async (payload) => {
          return await Services.FetchUserConfig(payload, tabId);
        });
        return true;

      case 'FETCH_STATIC_MESSAGES':
        handleAsyncMessage(request, sendResponse, async () => {
          return await Services.FetchStaticMessages();
        });
        return true;

      case 'UPDATE_USER_CONFIG':
        handleAsyncMessage(request, sendResponse, async (payload) => {
          return await Services.UpdateUserConfig(payload, tabId);
        });
        return true;

      case 'GET_AI_REPLY':
        handleAsyncMessage(request, sendResponse, async (payload) => {
          return await Services.fetchAiReply(payload, tabId);
        });
        return true;

      case 'CHAT_ARTIFACTS_FETCH':
        handleAsyncMessage(request, sendResponse, async (payload) => {
          return await Services.chatArtifactsManager.list(payload);
        });
        return true;

      case 'CHAT_ARTIFACTS_CREATE':
        handleAsyncMessage(request, sendResponse, async (payload) => {
          return await Services.chatArtifactsManager.create(payload);
        });
        return true;

      case 'CHAT_ARTIFACTS_UPDATE':
        handleAsyncMessage(request, sendResponse, async (payload) => {
          return await Services.chatArtifactsManager.update(payload);
        });
        return true;

      case 'CHAT_ARTIFACTS_DELETE':
        handleAsyncMessage(request, sendResponse, async (payload) => {
          return await Services.chatArtifactsManager.delete(payload);
        });
        return true;

      case 'POST_REPLY_REQUEST_ID':
        handleAsyncMessage(request, sendResponse, async (payload) => {
          await Services.PostReplyRequestId(payload, tabId);
          return null; // No data to return
        });
        return true;

      case 'GET_CHAT_SUMMARY':
        handleAsyncMessage(request, sendResponse, async (payload) => {
          return await Services.getChatSummary(payload, tabId);
        });
        return true;

      case 'SUBMIT_FEEDBACK':
        handleAsyncMessage(request, sendResponse, async (payload) => {
          return await Services.SubmitFeedback(payload, tabId);
        });
        return true;

      case 'FETCH_QUICK_REPLIES':
        handleAsyncMessage(request, sendResponse, async (payload) => {
          return await Services.fetchQuickReplies(payload, tabId);
        });
        return true;

      case 'SAVE_QUICK_REPLIES':
        handleAsyncMessage(request, sendResponse, async (payload) => {
          return await Services.saveQuickReplies(payload, tabId);
        });
        return true;

      case 'FETCH_SYSTEM_CONFIG':
        handleAsyncMessage(request, sendResponse, async () => {
          return await Services.FetchSystemConfig();
        });
        return true;

      case 'REMINDER_ACTION':
        handleAsyncMessage(request, sendResponse, async (payload) => {
          return await handleReminderAction(payload.action, payload.reminder);
        });
        return true;

      case 'CUSTOMER_CREATE':
        handleAsyncMessage(request, sendResponse, async (payload) => {
          return await Services.customerManager.create(payload);
        });
        return true;

      case 'CUSTOMER_UPDATE':
        handleAsyncMessage(request, sendResponse, async (payload) => {
          return await Services.customerManager.update(payload);
        });
        return true;

      case 'CUSTOMER_GET':
        handleAsyncMessage(request, sendResponse, async (payload) => {
          return await Services.customerManager.get(payload);
        });
        return true;

      case 'CUSTOMER_DELETE':
        handleAsyncMessage(request, sendResponse, async (payload) => {
          return await Services.customerManager.delete(payload);
        });
        return true;

      case 'COMPANIES_GET':
        handleAsyncMessage(request, sendResponse, async (payload) => {
          return await Services.customerManager.getCompanies(payload);
        });
        return true;

      case 'COMPANY_CREATE':
        handleAsyncMessage(request, sendResponse, async (payload) => {
          return await Services.customerManager.createCompany(payload);
        });
        return true;

      case 'COMPANY_UPDATE':
        handleAsyncMessage(request, sendResponse, async (payload) => {
          return await Services.customerManager.updateCompany(payload);
        });
        return true;

      case 'SCHEDULED_MESSAGE_DELIVERED':
        handleAsyncMessage(request, sendResponse, async (payload) => {
          return await handleScheduledMessageDeliverySuccess(payload);
        });
        return true;

      case 'SCHEDULED_MESSAGE_FAILED':
        handleAsyncMessage(request, sendResponse, async (payload) => {
          return await handleScheduledMessageDeliveryFailure(payload);
        });
        return true;
      
      case 'CHAT_DATA_COUNTS_INIT':
        handleAsyncMessage(request, sendResponse, async (payload) => {
          return await Services.chatArtifactsManager.initCounts(payload);
        });
        return true;

      case 'CHAT_DATA_COUNTS_GET':
        handleAsyncMessage(request, sendResponse, async (payload) => {
          return await Services.chatArtifactsManager.getCounts(payload);
        });
        return true;

      case 'DELETE_INDEXED_DB':
        handleAsyncMessage(request, sendResponse, async (payload) => {
          const { default: chatArtifactStorage } = await import('../services/ChatArtifacts/chatArtifactsStorage.js');
          await chatArtifactStorage.deleteIndexedDB(payload?.loggedUserId || null);
          return { ok: true };
        });
        return true;

      default:
        console.warn(`Unhandled message type: ${request.type}`);
        return false;
    }
  });
}

export { setupMessageListeners };
