import { setupMessageListeners } from './messageHandlers.js';
import { errorHandler } from './errorHandler.js';
import { setupReminderChecker } from './services/ReminderChecker.js';
import { setupScheduledMessageChecker } from './services/ScheduledMessageChecker.js';
import { DB_CONFIG } from '../Constants/constant.js';
import { IndexedDBUtils } from '../utils/indexedDBUtils.js';
// import '../utils/IndexedDBConsole.js';

// Log that storage services are available
console.log(`Background services initialized at ${new Date().toLocaleString()}`);

// Function to get user data from IndexedDB
const getUserDataFromIndexedDB = async () => {
  try {
    const stores = [
      { 
        name: DB_CONFIG.STORES.USER_DATA, 
        options: { keyPath: 'id' } 
      }
    ];
    
    const db = await IndexedDBUtils.openDatabase(DB_CONFIG.NAME, DB_CONFIG.VERSION, stores);
    const userData = await IndexedDBUtils.getFromStore(db, DB_CONFIG.STORES.USER_DATA, 'currentUser');
    db.close();
    
    return userData;
  } catch (error) {
    console.warn('Failed to get user data from IndexedDB:', error);
    return null;
  }
};


// Function to refresh WhatsApp Web tabs and initialize authentication
const refreshWhatsAppTabs = async () => {
  try {
    const tabs = await chrome.tabs.query({ url: '*://web.whatsapp.com/*' });
    
    for (const tab of tabs) {
      console.log(`Refreshing WhatsApp tab: ${tab.id}`);
      await chrome.tabs.reload(tab.id);
    }
    
    if (tabs.length > 0) {
      console.log(`Refreshed ${tabs.length} WhatsApp tab(s) after service worker reload`);
    }
  } catch (error) {
    console.error('Error refreshing WhatsApp tabs:', error);
  }
};

// Function to setup reminder and scheduled message services when user data is available
const setupServices = async () => {
  try {
    const userData = await getUserDataFromIndexedDB();

    if (userData && userData.userId && userData.userNumber && userData.token) {
      console.log('User data available, setting up services');
      setupReminderChecker();
      setupScheduledMessageChecker();
    } else {
      console.log('User data not available, skipping services setup');
      // Retry after some time
      setTimeout(setupServices, 5000);
    }
  } catch (error) {
    console.error('Error setting up services:', error);
  }
};

// Initialize services
setupMessageListeners();
// errorHandler();

// Refresh WhatsApp tabs when service worker starts
// refreshWhatsAppTabs();

// Setup services when user data becomes available
setTimeout(setupServices, 3000);

