import ChatArtifactService from "./chatArtifactsService";
import chatArtifactStorage from "./chatArtifactsStorage";
import { sendMessageToWhatsAppTabs } from "../../utils/sendMessageToWhatsappTab";

class ChatArtifactsManager{

    // Configuration for status-aware data types
    static STATUS_CONFIGS = {
        'to_do': {
            statuses: ['pending', 'done'],
            primaryStatus: 'pending',
            secondaryStatus: 'done'
        },
        'reminders': {
            statuses: ['pending', 'processed'],
            primaryStatus: 'pending',
            secondaryStatus: 'processed'
        },
        'scheduled_messages': {
            statuses: ['pending', 'processed'],
            primaryStatus: 'pending',
            secondaryStatus: 'processed'
        }
    };

    // Check if data type supports dual status handling
    _isStatusAware(dataType) {
        return ChatArtifactsManager.STATUS_CONFIGS.hasOwnProperty(dataType);
    }

    // Get status configuration for a data type
    _getStatusConfig(dataType) {
        return ChatArtifactsManager.STATUS_CONFIGS[dataType];
    }

    // Update local storage item with proper status handling
    async _updateLocalItem(artifactId, mainContent, status, metaData) {
        const localItem = await chatArtifactStorage.findByApiId(artifactId);
        if (localItem) {
            await chatArtifactStorage.updateChatItem(localItem.id, {
                main_content: mainContent,
                status: status,
                meta_data: metaData
            });
        }
        return localItem;
    }

    // Fetch and push dual status data to frontend
    async _fetchAndPushDualStatusData(chatId, dataType, paginationMeta) {
        if (!this._isStatusAware(dataType)) {
            // Fallback for non-status-aware types
            const localData = await chatArtifactStorage.getChatData(chatId, dataType);
            this.pushToFrontend(chatId, dataType, localData, paginationMeta);
            return;
        }

        const config = this._getStatusConfig(dataType);

        // Get data for all statuses in configuration
        for (const status of config.statuses) {
            const statusData = await chatArtifactStorage.getChatData(chatId, dataType, { status });
            const statusMeta = { ...paginationMeta, status };
            this.pushToFrontend(chatId, dataType, statusData, statusMeta);
        }
    }

    async initialize(payload){
        const userId = payload.loggedUserId
        await chatArtifactStorage.initialize(userId);
    }

    // Initialize counts store once per user; fetch from API if empty
    async initCounts(payload = {}) {
        try {
            await this.initialize(payload);
            const hasCounts = await chatArtifactStorage.hasAnyCountsData();
            if (hasCounts) {
                return { skipped: true };
            }
            const counts = await ChatArtifactService.counts(payload);
            await chatArtifactStorage.bulkSaveCountsFromApi(counts);
            return { success: true };
        } catch (error) {
            console.error('Error initializing artifact counts:', error);
            return { success: false, error: error.message };
        }
    }

    async list(payload){
        try {
            await this.initialize(payload);
            const isInitialLoad = !payload.page || payload.page === 1;
            const chatId = payload.chatId
            const data_type = payload.data_type
            const status = payload.status

            if (isInitialLoad) {
                // Clear local storage only for this chat and data type
                // For status-aware data types, clear status-specific data
                if (this._isStatusAware(data_type) && status) {
                    await chatArtifactStorage.clearChatData(chatId, data_type, { status });
                } else {
                    await chatArtifactStorage.clearChatData(chatId, data_type);
                }
            }

            // Get local data with status filtering for status-aware data types
            const localDataOptions = this._isStatusAware(data_type) && status ? { status } : {};
            const localData = await chatArtifactStorage.getChatData(chatId, data_type, localDataOptions);

            if (localData && localData.length > 0) {
                const paginationMeta = {
                    hasNextPage: false,
                    count: localData.length,
                    next: null,
                    previous: null,
                    currentPage: 1,
                    status: status
                };
                this.pushToFrontend(chatId, data_type, localData, paginationMeta);
            }
            
            // Always fetch fresh data from API for consistency
            const apiResponse = await ChatArtifactService.list(payload);
            const apiData = apiResponse.data;
            
            const dataToSend = [...localData, ...apiData]
            const paginationMeta = {
                hasNextPage: !!apiResponse.next,
                count: apiResponse.count || 0,
                next: apiResponse.next,
                previous: apiResponse.previous,
                currentPage: payload.page || 1,
                status: status
            };
            this.pushToFrontend(chatId, data_type, dataToSend, paginationMeta);
            
            const localDataMap = new Map(localData.map(item => [item.apiId, item]));
            
            for (const apiItem of apiData) {
                const localItem = localDataMap.get(apiItem.id);
                if (!localItem || new Date(apiItem.updated_at) > new Date(localItem.lastUpdated)) {
                    await chatArtifactStorage.saveChatData({
                        ...apiItem,
                        apiId: apiItem.id,
                        chatId: apiItem.chat_id,
                        dataType: apiItem.data_type,
                        status: apiItem.status // Save status for todos
                    });
                }
            }

            return apiResponse;
        } catch (error) {
            console.error('Error in list method:', error);
            throw error;
        }
    }

    async create(payload){
        try {
            await this.initialize(payload);
            const chatId = payload.chatId;
            const data_type = payload.data_type;
            const main_content = payload.main_content;
            const original_payload = structuredClone(payload);
            await ChatArtifactService.create({
                ...payload,
                chat_id: chatId,
                data_type: data_type,
                main_content
            });
            // Increment count for the artifact
            try { await chatArtifactStorage.incrementCount(chatId, data_type, 1); } catch (e) { console.warn('Count increment failed', e); }
            return this.list(original_payload);
        } catch (error) {
            console.error('Error in create method:', error);
            return { success: false, error: error.message };
        }
    }

    async update(payload){
        try {
            await this.initialize(payload);
            const chatId = payload.chatId;
            const data_type = payload.data_type; 
            const artifact_id = payload.artifact_id
            const main_content = payload.main_content;
            const paginationMeta = payload.paginationMeta || {};
            

            await ChatArtifactService.update(artifact_id, payload);

            // Update local storage item with status
            if (this._isStatusAware(data_type)) {
                const previousLocalItem = await this._updateLocalItem(artifact_id, main_content, payload.status, payload.meta_data);
                const prevStatus = previousLocalItem?.status;
                const newStatus = payload.status;
                

                // Adjust counts based only on new status per requirement
                try {
                    const primary = this._getStatusConfig(data_type).primaryStatus; // typically 'pending'
                    if (newStatus === primary) {
                        await chatArtifactStorage.incrementCount(chatId, data_type, 1);
                    } else {
                        await chatArtifactStorage.decrementCount(chatId, data_type, 1);
                    }
                } catch (e) {
                    console.warn('Count update on status change failed', e);
                }
                // Fetch and push both status lists for dual-status data types
                await this._fetchAndPushDualStatusData(chatId, data_type, paginationMeta);
            } else {
                // For other data types, use the existing logic
                await chatArtifactStorage.updateChatItemByArtifactId(chatId, data_type, artifact_id, main_content);
                const localData = await chatArtifactStorage.getChatData(chatId, data_type);
                this.pushToFrontend(chatId, data_type, localData, paginationMeta);
            }

        } catch (error) {
            console.error('Error in update method:', error);
            return { success: false, error: error.message };
        }
    }

    async delete(payload){
        try {
            await this.initialize(payload);
            const artifact_id = payload.artifact_id;
            const chatId = payload.chatId;
            const data_type = payload.data_type; 
            const paginationMeta = payload.paginationMeta || {};

            await ChatArtifactService.delete(artifact_id, payload);
            
            // Find the local item by apiId and delete it
            const localItem = await chatArtifactStorage.findByApiId(artifact_id);
            if (localItem) {
                await chatArtifactStorage.deleteChatData(localItem.id);
            }

            // Decrement count for the artifact
            try { await chatArtifactStorage.decrementCount(chatId, data_type, 1); } catch (e) { console.warn('Count decrement failed', e); }

            // Refresh and push data based on data type
            await this._fetchAndPushDualStatusData(chatId, data_type, paginationMeta);

        } catch (error) {
            console.error('Error in delete method:', error);
            return { success: false, error: error.message };
        }
    }

    async getCounts(payload){
        try {
            await this.initialize(payload);
            const chatId = payload.chatId;
            if (!chatId) return { toDo: 0, notes: 0, reminders: 0, scheduled_messages: 0};
            const rec = await chatArtifactStorage.getCountsByChat(chatId);
            return {
                toDo: rec?.toDo || 0,
                notes: rec?.notes || 0,
                reminders: rec?.reminders || 0,
                scheduled_messages: rec?.scheduled_messages || 0
            };
        } catch (error) {
            console.error('Error in getCounts method:', error);
            return { toDo: 0, notes: 0, reminders: 0, scheduled_messages: 0};
        }
    }

    pushToFrontend(chatId, dataType, payload, paginationMeta={}){
        sendMessageToWhatsAppTabs({
            type: 'CHAT_ARTIFACT_UPDATE',
            chatId,
            dataType,
            data: payload,
            paginationMeta: paginationMeta
        });
    }
}

const chatArtifactsManager = new ChatArtifactsManager();
export default chatArtifactsManager;
