import { captureException } from "../utils/sentry";
import { encryptData } from "./EncryptDecrypt";
import { GenerateToken } from "../services";
import { IS_PRODUCTION, DB_CONFIG } from "../Constants/constant";
import { IndexedDBUtils } from "./indexedDBUtils";

const getUserDataFromIndexedDB = async () => {
  try {
    const stores = [
      { 
        name: DB_CONFIG.STORES.USER_DATA, 
        options: { keyPath: 'id' } 
      }
    ];
    
    const db = await IndexedDBUtils.openDatabase(DB_CONFIG.NAME, DB_CONFIG.VERSION, stores);
    const userData = await IndexedDBUtils.getFromStore(db, DB_CONFIG.STORES.USER_DATA, 'currentUser');
    db.close();
    
    return userData;
  } catch (error) {
    console.warn('Failed to get user data from IndexedDB:', error);
    return null;
  }
};

async function makeRequest(url, method, payload) {
  const headers = {};

  if (payload?.loggedUserNumber) {
    headers['X-Number'] = encryptData(payload.loggedUserNumber);
    delete payload.loggedUserNumber;
  }

  if (payload?.loggedToken) {
    headers['X-Session'] = payload.loggedToken;
    delete payload.loggedToken;
  }

  if (IS_PRODUCTION) {
    headers['X-Environment'] = 'production';
  }

  const options = {
    method,
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
      ...headers
    }
  };

  if (payload !== null && payload !== undefined && method !== 'GET' && method !== 'HEAD') {
    options.body = JSON.stringify(payload);
  }

  return await fetch(url, options);
}

export const apiRequest = async (url, method = 'GET', payload = null, tabId = null) => {
  let userId = null;
  let loggedUserNumber = null;
  let requestType = null;
  let session = null;
  let response = null;
  let res_data = null;

  try {
    if (payload?.loggedUserId) {
      userId = payload.loggedUserId;
      delete payload.loggedUserId;
    }
    if (payload?.requestType) {
      requestType = payload.requestType;
      delete payload.requestType;
    }
    if (payload?.loggedUserNumber) {
      loggedUserNumber = payload.loggedUserNumber;
    }
    if (payload?.loggedToken) {
      session = payload.loggedToken;
    }

    // Get user data from IndexedDB if not available in payload
    if (!userId || !loggedUserNumber || !payload?.loggedToken) {
      const userData = await getUserDataFromIndexedDB();
      if (userData) {
        if (!userId) userId = userData.userId;
        if (!loggedUserNumber) {
          loggedUserNumber = userData.userNumber;
          // Add userNumber to payload so it gets passed to makeRequest for X-Number header
          if (loggedUserNumber) {
            payload = payload || {};
            payload.loggedUserNumber = loggedUserNumber;
          }
        }
        if (!payload?.loggedToken && userData.token) {
          payload = payload || {};
          payload.loggedToken = userData.token;
        }
      }
    }

    response = await makeRequest(url, method, payload);

    if (response.status === 401 && userId) {
      const tokenResponse = await GenerateToken(userId, tabId, loggedUserNumber);

      if (tokenResponse?.token) {
        session = tokenResponse.token;
        payload.loggedToken = tokenResponse.token;
        payload.loggedUserNumber = loggedUserNumber;
        response = await makeRequest(url, method, payload);
      } else {
        throw new Error('Failed to generate token');
      }
    }

    res_data = await response.json();

    if (!response.ok) {
      throw new Error(`Request failed with status ${response.status}: error ${res_data.error || 'Unknown error'}`);
    }

    return res_data;
  } catch (error) {
    // Don't capture 404 errors for GET customer requests (customer not found is expected)
    const isGetCustomer = method === 'GET' && url.includes('/customers/');
    const is404Error = response && response.status === 404;
    // Don't capture 400 validation errors for company mutations
    const isCompanyMutation = method === 'POST' && url.includes('/company') || method === 'PATCH' && url.includes('/company/');
    const is400Error = response && response.status === 400;
    const is401Error = response && response.status === 401;
    
    if (!((isGetCustomer && is404Error) || (isCompanyMutation && is400Error) || is401Error)) {
      const context = {
        user: { id: userId, phoneNumber: loggedUserNumber },
        tags: {
          module: 'apiRequest',
        },
        extra: {
          url: url,
          method: method,
          session: session,
          requestType: requestType || 'unknown',
          statusCode: response?.status || 'unknown',
          responseBody: res_data || response || 'unknown',
        }
      };

      captureException(error, context);
    }
    
    throw new Error(error.message);
  }
};
