import CryptoJS from 'crypto-js';
import { AES_SECRET_KEY } from '../Constants/constant'

if (!AES_SECRET_KEY) {
  throw new Error('Missing REACT_APP_AES_SECRET_KEY in .env');
}

const key = CryptoJS.enc.Base64.parse(AES_SECRET_KEY);

export const encryptData = (data) => {
  const iv = CryptoJS.lib.WordArray.random(16);

  const encrypted = CryptoJS.AES.encrypt(data, key, {
    iv,
    mode: CryptoJS.mode.CBC,
    padding: CryptoJS.pad.Pkcs7,
  });

  const combined = iv.concat(encrypted.ciphertext);
  return CryptoJS.enc.Base64.stringify(combined);
};

export const decryptData = (encryptedBase64) => {
  const rawData = CryptoJS.enc.Base64.parse(encryptedBase64);
  const iv = CryptoJS.lib.WordArray.create(rawData.words.slice(0, 4), 16);
  const ciphertext = CryptoJS.lib.WordArray.create(rawData.words.slice(4));

  const decrypted = CryptoJS.AES.decrypt({ ciphertext }, key, {
    iv,
    mode: CryptoJS.mode.CBC,
    padding: CryptoJS.pad.Pkcs7,
  });

  return decrypted.toString(CryptoJS.enc.Utf8);
};
