import { encryptData } from '../utils/EncryptDecrypt';
import { BACKEND_API_URL } from '../Constants/constant'
import { apiRequest } from '../utils/apiRequest';

export const FetchStaticMessages = async () => {
  try {
    return await apiRequest(BACKEND_API_URL.FETCH_STATIC_MESSAGES, 'GET');
  } catch (err) {
    console.error('Error FetchStaticMessages:', err);
    throw new Error(`${err.message}`);
  }
};
