import {
  BrowserClient,
  defaultStackParser,
  getDefaultIntegrations,
  makeFetchTransport,
  withScope,
  captureException as sentryCaptureException,
  setCurrentClient,
} from '@sentry/browser';
import { IS_PRODUCTION } from '../Constants/constant';

let client;

const SENTRY_DSN = 'https://d1d49d8c742d0cba9241330efa314854@o4509061130944512.ingest.de.sentry.io/4509061208539216'

function createSentryClient() {
  const integrations = getDefaultIntegrations().filter(
    ({ name }) => !['BrowserApiErrors', 'Breadcrumbs', 'GlobalHandlers'].includes(name)
  );

  return new BrowserClient({
    dsn: SENTRY_DSN,
    transport: makeFetchTransport,
    stackParser: defaultStackParser,
    integrations,
  });
}

export function initSentry() {
  if (!IS_PRODUCTION) return;
  if (!client) {
    client = createSentryClient();
    setCurrentClient(client);
  }
}

export function captureException(error, context = {}) {
  initSentry();

  if (client) {
    withScope(scope => {
      if (context.user) scope.setUser(context.user);
      if (context.tags) scope.setTags(context.tags);
      if (context.extra) scope.setExtras(context.extra);

      sentryCaptureException(error);
    });
  } else {
    console.error('Sentry Error:', error, context);
  }
}
