export function sendMessageToWhatsAppTabs(message) {
  // Check if chrome.tabs API is available
  if (!chrome || !chrome.tabs || !chrome.tabs.sendMessage) {
    console.error('Chrome tabs API not available');
    return;
  }

  chrome.tabs.query({}, (tabs) => {
    if (chrome.runtime.lastError) {
      console.error('Error querying tabs:', chrome.runtime.lastError.message);
      return;
    }

    tabs.forEach((tab) => {
      if (tab.url && tab.url.includes('web.whatsapp.com')) {
        try {
          chrome.tabs.sendMessage(tab.id, message, () => {
            if (chrome.runtime.lastError) {
              // Don't log error for disconnected tabs as it's normal behavior
              if (!chrome.runtime.lastError.message.includes('Could not establish connection')) {
                console.error(`Error sending message to tab ${tab.id}:`, chrome.runtime.lastError.message);
              }
            }
          });
        } catch (error) {
          console.error(`Failed to send message to tab ${tab.id}:`, error.message);
        }
      }
    });
  });
}