import { encryptData } from '../utils/EncryptDecrypt';
import { BACKEND_API_URL } from '../Constants/constant';
import { apiRequest } from '../utils/apiRequest';

export const Onboarding = async (formData) => {
  try {
    formData.number = encryptData(formData.number);
    return await apiRequest(BACKEND_API_URL.USER_ONBOARDING, 'POST', formData);
  } catch (err) {
    console.error('Error Onboarding:', err);
    throw new Error(`${err.message}`);
  }
};
