import { encryptData } from '../utils/EncryptDecrypt';
import { BACKEND_API_URL, DB_CONFIG } from '../Constants/constant';
import { apiRequest } from '../utils/apiRequest';
import { IndexedDBUtils } from '../utils/indexedDBUtils';

const saveUserDataToIndexedDB = async (userId, userNumber) => {
  const stores = [
    { 
      name: DB_CONFIG.STORES.USER_DATA, 
      options: { keyPath: 'id' } 
    }
  ];
  
  const db = await IndexedDBUtils.openDatabase(DB_CONFIG.NAME, DB_CONFIG.VERSION, stores);
  
  // Get existing data to preserve token if it exists
  let existingData = null;
  try {
    existingData = await IndexedDBUtils.getFromStore(db, DB_CONFIG.STORES.USER_DATA, 'currentUser');
  } catch (error) {
    console.warn('No existing user data found');
  }
  
  const userData = {
    id: 'currentUser',
    userId,
    userNumber,
    token: existingData?.token || null,
    timestamp: Date.now()
  };
  
  await IndexedDBUtils.putToStore(db, DB_CONFIG.STORES.USER_DATA, userData);
  db.close();
};

export const UserLogin = async (phoneNumber) => {
  try {
    const encryptedUserId = encryptData(phoneNumber);
    const payload = { number: encryptedUserId };
    const response = await apiRequest(BACKEND_API_URL.USER_LOGIN, 'POST', payload);
    
    if (response?.user_id) {
      await saveUserDataToIndexedDB(response.user_id, phoneNumber);
    }
    
    return response;
  } catch (error) {
    console.error('Error UserLogin:', error);
    throw new Error(error.message);
  }
};
