import { BACKEND_API_URL} from '../Constants/constant';
import { apiRequest } from '../utils/apiRequest';


export const PostReplyRequestId = async (payload, tabId) => {
  try {
    return await apiRequest(BACKEND_API_URL.POST_REPLY_REQUEST_ID(payload.requestId), 'PATCH', payload, tabId);
  } catch (err) {
    console.error('Error PostReplyRequestId:', err);
    throw new Error(`${err.message}`);
  }
};
