import { BACKEND_API_URL } from '../Constants/constant'
import { apiRequest } from '../utils/apiRequest';

export const getChatSummary = async (payload, tabId) => {
  try {
    return await apiRequest(BACKEND_API_URL.FETCH_CHAT_SUMMARY, 'POST', payload, tabId);
  } catch (err) {
    console.error('Error getChatSummary:', err);
    throw new Error(`${err.message}`);
  }
};
