import { DB_CONFIG } from '../../Constants/constant.js';
import { IndexedDBUtils } from '../../utils/indexedDBUtils.js';

const DB_NAME = DB_CONFIG.NAME;
const DB_VERSION = DB_CONFIG.VERSION;
const STORE_NAME = DB_CONFIG.STORES.CHAT_DATA;
const USER_STORE_NAME = DB_CONFIG.STORES.USER_META;
const COUNT_STORE_NAME = DB_CONFIG.STORES.CHAT_DATA_COUNT;

class ChatArtifactStorage {
  constructor() {
    this.db = null;
    this.currentUserId = null;
    this.isInitialized = false;
  }

  _mapArtifactKey(artifact) {
    if (artifact === 'to_do') return 'toDo';
    return artifact;
  }

  async initialize(userId) {
    if(this.isInitialized) return;
    if (!userId) throw new Error('User ID is required for initialization');
    if (this.currentUserId && this.currentUserId !== userId) {
      await this.clearAllData();
    }

    this.currentUserId = userId;
    if (!this.db) {
      await this.openDatabase();
    }

    await this.setUserMeta(userId);
    this.isInitialized = true;
  }

  async openDatabase() {
    return new Promise((resolve, reject) => {
      const request = indexedDB.open(DB_NAME+"_"+this.currentUserId, DB_VERSION);

      request.onerror = () => reject(new Error('Failed to open IndexedDB'));
      request.onsuccess = (event) => {
        this.db = event.target.result;
        resolve(this.db);
      };
      request.onupgradeneeded = (event) => {
        const db = event.target.result;

        if (!db.objectStoreNames.contains(STORE_NAME)) {
          const store = db.createObjectStore(STORE_NAME, { keyPath: 'id', autoIncrement: true });
          store.createIndex('chatId', 'chatId', { unique: false });
          store.createIndex('dataType', 'dataType', { unique: false });
          store.createIndex('userId', 'userId', { unique: false });
          store.createIndex('apiId', 'apiId', { unique: false });
          store.createIndex('lastUpdated', 'lastUpdated', { unique: false });
          store.createIndex('status', 'status', { unique: false });
        }

        if (!db.objectStoreNames.contains(USER_STORE_NAME)) {
          db.createObjectStore(USER_STORE_NAME, { keyPath: 'key' });
        }

        // Create counts store if missing: one record per chatId for O(1) access
        if (!db.objectStoreNames.contains(COUNT_STORE_NAME)) {
          const countStore = db.createObjectStore(COUNT_STORE_NAME, { keyPath: 'chatId' });
          countStore.createIndex('chatId', 'chatId', { unique: true });
        }
      };
    });
  }

  ensureInitialized() {
    if (!this.isInitialized || !this.db) {
      throw new Error('ChatArtifactStorage not initialized. Call initialize(userId) first.');
    }
  }

  async setUserMeta(userId) {
    const transaction = this.db.transaction([USER_STORE_NAME], 'readwrite');
    const store = transaction.objectStore(USER_STORE_NAME);
    await store.put({
      key: 'currentUser',
      userId,
      lastAccess: new Date().toISOString()
    });
  }

  async getUserMeta() {
    const transaction = this.db.transaction([USER_STORE_NAME], 'readonly');
    const store = transaction.objectStore(USER_STORE_NAME);
    return new Promise((resolve, reject) => {
      const request = store.get('currentUser');
      request.onsuccess = () => resolve(request.result);
      request.onerror = () => reject(request.error);
    });
  }

  // ===== Counts Store Helpers =====
  async hasAnyCountsData() {
    this.ensureInitialized();
    if (!this.db.objectStoreNames.contains(COUNT_STORE_NAME)) return false;
    const transaction = this.db.transaction([COUNT_STORE_NAME], 'readonly');
    const store = transaction.objectStore(COUNT_STORE_NAME);
    return new Promise((resolve) => {
      const request = store.openCursor();
      request.onsuccess = () => {
        const cursor = request.result;
        resolve(!!cursor);
      };
      request.onerror = () => resolve(false);
    });
  }

  async incrementCount(chatId, artifact, by = 1) {
    this.ensureInitialized();
    const field = this._mapArtifactKey(artifact);
    const transaction = this.db.transaction([COUNT_STORE_NAME], 'readwrite');
    const store = transaction.objectStore(COUNT_STORE_NAME);
    return new Promise((resolve, reject) => {
      const getReq = store.get(chatId);
      getReq.onsuccess = () => {
        const record = getReq.result || { chatId };
        const current = Number(record[field] || 0);
        const next = current + by;
        record[field] = next;
        const putReq = store.put(record);
        putReq.onsuccess = () => { resolve(true); };
        putReq.onerror = () => { reject(putReq.error); };
      };
      getReq.onerror = () => { reject(getReq.error); };
    });
  }

  async decrementCount(chatId, artifact, by = 1) {
    this.ensureInitialized();
    const field = this._mapArtifactKey(artifact);
    const transaction = this.db.transaction([COUNT_STORE_NAME], 'readwrite');
    const store = transaction.objectStore(COUNT_STORE_NAME);
    return new Promise((resolve, reject) => {
      const getReq = store.get(chatId);
      getReq.onsuccess = () => {
        const record = getReq.result || { chatId };
        const current = Number(record[field] || 0);
        const next = current - by;
        const clamped = next > 0 ? next : 0;
        record[field] = clamped;
        const putReq = store.put(record);
        putReq.onsuccess = () => { resolve(true); };
        putReq.onerror = () => { reject(putReq.error); };
      };
      getReq.onerror = () => { reject(getReq.error); };
    });
  }

  async upsertCountForChat(chatId, { toDo = 0, notes = 0, reminders = 0, scheduled_messages = 0 } = {}) {
    this.ensureInitialized();
    const transaction = this.db.transaction([COUNT_STORE_NAME], 'readwrite');
    const store = transaction.objectStore(COUNT_STORE_NAME);
    const record = { chatId, toDo, notes, reminders, scheduled_messages };
    return new Promise((resolve, reject) => {
      const request = store.put(record);
      request.onsuccess = () => resolve(true);
      request.onerror = () => reject(request.error);
    });
  }

  async bulkSaveCountsFromApi(countsMap = {}) {
    // countsMap example: { '9195@c.us': { to_do: 0, notes: 2, reminders: 0 }, ... }
    const entries = Object.entries(countsMap || {});
    for (const [chatId, value] of entries) {
      const normalized = {
        toDo: Number(value?.to_do || 0),
        notes: Number(value?.notes || 0),
        reminders: Number(value?.reminders || 0),
        scheduled_messages: Number(value?.scheduled_messages || 0)
      };
      await this.upsertCountForChat(chatId, normalized);
    }
    return true;
  }

  async getCountsByChat(chatId) {
    this.ensureInitialized();
    if (!this.db.objectStoreNames.contains(COUNT_STORE_NAME)) return null;
    const transaction = this.db.transaction([COUNT_STORE_NAME], 'readonly');
    const store = transaction.objectStore(COUNT_STORE_NAME);
    return new Promise((resolve, reject) => {
      const request = store.get(chatId);
      request.onsuccess = () => resolve(request.result || null);
      request.onerror = () => reject(request.error);
    });
  }

  async saveChatData(data) {
    this.ensureInitialized();
    const transaction = this.db.transaction([STORE_NAME], 'readwrite');
    const store = transaction.objectStore(STORE_NAME);
    const dataToStore = {
      ...data,
      userId: this.currentUserId,
      lastUpdated: new Date().toISOString()
    };
    return new Promise((resolve, reject) => {
      const request = store.put(dataToStore);
      request.onsuccess = () => resolve(request.result);
      request.onerror = () => reject(request.error);
    });
  }

  async getChatData(chatId, dataType, options = {}) {
    this.ensureInitialized();
    const { status } = options;
    const transaction = this.db.transaction([STORE_NAME], 'readonly');
    const store = transaction.objectStore(STORE_NAME);

    return new Promise((resolve, reject) => {
      const results = [];
      const index = chatId ? store.index('chatId') : store;
      const cursorRequest = chatId
        ? index.openCursor(IDBKeyRange.only(chatId))
        : index.openCursor();

      cursorRequest.onsuccess = (event) => {
        const cursor = event.target.result;
        if (cursor) {
          const data = cursor.value;
          if (data.userId === this.currentUserId &&
              (!dataType || data.dataType === dataType) &&
              (!status || !dataType || (dataType !== 'to_do' && dataType !== 'reminders' && dataType !== 'scheduled_messages') || data.status === status)) {
            results.push(data);
          }
          cursor.continue();
        } else {
          results.sort((a, b) => b.id - a.id);
          resolve(results);
        }
      };
      cursorRequest.onerror = () => reject(cursorRequest.error);
    });
  }

  async updateChatItemByArtifactId(chatId, dataType, artifactId, main_content) {
    this.ensureInitialized();
  
    const transaction = this.db.transaction([STORE_NAME], 'readwrite');
    const store = transaction.objectStore(STORE_NAME);
    const index = store.index('chatId');
    const keyRange = IDBKeyRange.only(chatId);
  
    return new Promise((resolve, reject) => {
      const cursorRequest = index.openCursor(keyRange);
  
      cursorRequest.onsuccess = (event) => {
        const cursor = event.target.result;
        if (!cursor) {
          return reject(new Error(`Artifact with ID ${artifactId} not found for chat ${chatId}`));
        }
  
        const data = cursor.value;
  
        const isMatch = (
          data.userId === this.currentUserId &&
          data.dataType === dataType &&
          data.apiId === artifactId
        );
  
        if (isMatch) {
          const updatedItem = {
            ...data,
            main_content,
            lastUpdated: new Date().toISOString()
          };
  
          const updateRequest = cursor.update(updatedItem);
          updateRequest.onsuccess = () => resolve(updatedItem);
          updateRequest.onerror = () => reject(updateRequest.error);
        } else {
          cursor.continue();
        }
      };
  
      cursorRequest.onerror = () => reject(cursorRequest.error);
    });
  }  

  async deleteChatData(id) {
    this.ensureInitialized();
    const transaction = this.db.transaction([STORE_NAME], 'readwrite');
    const store = transaction.objectStore(STORE_NAME);

    return new Promise((resolve, reject) => {
      const getRequest = store.get(id);
      getRequest.onsuccess = () => {
        const data = getRequest.result;
        if (!data || data.userId !== this.currentUserId) {
          reject(new Error('Data not found or access denied'));
          return;
        }

        const deleteRequest = store.delete(id);
        deleteRequest.onsuccess = () => resolve(true);
        deleteRequest.onerror = () => reject(deleteRequest.error);
      };
      getRequest.onerror = () => reject(getRequest.error);
    });
  }

  async clearChatData(chatId, dataType, options = {}) {
    this.ensureInitialized();
    const { status } = options;
  
    const transaction = this.db.transaction([STORE_NAME], 'readwrite');
    const store = transaction.objectStore(STORE_NAME);
    const index = chatId ? store.index('chatId') : store;
    const keyRange = chatId ? IDBKeyRange.only(chatId) : null;
  
    return new Promise((resolve, reject) => {
      const cursorRequest = keyRange ? index.openCursor(keyRange) : index.openCursor();
  
      cursorRequest.onsuccess = (event) => {
        const cursor = event.target.result;
        if (cursor) {
          const data = cursor.value;
  
          // Apply the same filtering as in getChatData, including status for todos and reminders
          if (
            data.userId === this.currentUserId &&
            (!dataType || data.dataType === dataType) &&
            (!status || !dataType || (dataType !== 'to_do' && dataType !== 'reminders' && dataType !== 'scheduled_messages') || data.status === status)
          ) {
            cursor.delete(); // delete current record
          }
  
          cursor.continue();
        } else {
          // No more entries
          transaction.oncomplete = () => resolve(true);
          transaction.onerror = () => reject(transaction.error);
        }
      };
  
      cursorRequest.onerror = () => reject(cursorRequest.error);
    });
  }

  async findByApiId(apiId) {
    this.ensureInitialized();
    const transaction = this.db.transaction([STORE_NAME], 'readonly');
    const store = transaction.objectStore(STORE_NAME);
    const index = store.index('apiId');

    return new Promise((resolve, reject) => {
      const request = index.get(apiId);
      request.onsuccess = () => {
        const result = request.result;
        resolve(result && result.userId === this.currentUserId ? result : null);
      };
      request.onerror = () => reject(request.error);
    });
  }

  async findById(id) {
    this.ensureInitialized();
    const transaction = this.db.transaction([STORE_NAME], 'readonly');
    const store = transaction.objectStore(STORE_NAME);

    return new Promise((resolve, reject) => {
      const request = store.get(id);
      request.onsuccess = () => {
        const result = request.result;
        resolve(result && result.userId === this.currentUserId ? result : null);
      };
      request.onerror = () => reject(request.error);
    });
  }

  async clearAllData() {
    if (!this.db) return;

    const transaction = this.db.transaction([STORE_NAME, USER_STORE_NAME], 'readwrite');
    await Promise.all([
      new Promise((resolve, reject) => {
        const request = transaction.objectStore(STORE_NAME).clear();
        request.onsuccess = () => resolve();
        request.onerror = () => reject(request.error);
      }),
      new Promise((resolve, reject) => {
        const request = transaction.objectStore(USER_STORE_NAME).clear();
        request.onsuccess = () => resolve();
        request.onerror = () => reject(request.error);
      })
    ]);
  }

  async updateChatItem(id, updates) {
    this.ensureInitialized();
  
    const transaction = this.db.transaction([STORE_NAME], 'readwrite');
    const store = transaction.objectStore(STORE_NAME);
  
    return new Promise((resolve, reject) => {
      const getRequest = store.get(id);
  
      getRequest.onsuccess = () => {
        const existingItem = getRequest.result;
  
        if (!existingItem) {
          return reject(new Error(`Item with id ${id} not found`));
        }
  
        const updatedItem = {
          ...existingItem,
          ...updates,
          lastUpdated: new Date().toISOString() // optional timestamp update
        };
  
        const putRequest = store.put(updatedItem);
  
        putRequest.onsuccess = () => resolve(updatedItem);
        putRequest.onerror = () => reject(putRequest.error);
      };
  
      getRequest.onerror = () => reject(getRequest.error);
    });
  }  

  close() {
    if (this.db) {
      this.db.close();
      this.db = null;
      this.isInitialized = false;
    }
  }

  async deleteIndexedDB(userId = null) {
    // Close any open connection
    this.close();

    const deleteDb = (name) => new Promise((resolve) => {
      try {
        const req = indexedDB.deleteDatabase(name);
        req.onsuccess = req.onerror = req.onblocked = () => resolve(true);
      } catch (_) { resolve(false); }
    });

    // Delete per-user artifacts DB if user id is available
    let uid = userId || this.currentUserId || null;
    // If userId not provided/known, try to read it from shared DB userData store
    if (!uid) {
      try {
        const stores = [ { name: DB_CONFIG.STORES.USER_DATA, options: { keyPath: 'id' } } ];
        const db = await IndexedDBUtils.openDatabase(DB_NAME, DB_VERSION, stores);
        const userData = await IndexedDBUtils.getFromStore(db, DB_CONFIG.STORES.USER_DATA, 'currentUser');
        db.close();
        uid = userData?.userId || null;
      } catch (_) { /* noop */ }
    }
    if (uid) {
      await deleteDb(DB_NAME + '_' + uid);
    }
    // Also delete shared DB used for user meta and other stores
    await deleteDb(DB_NAME);
    return true;
  }
}

// Export singleton instance
const chatArtifactStorage = new ChatArtifactStorage();
export default chatArtifactStorage;
