import { BACKEND_API_URL } from '../../Constants/constant'
import { apiRequest } from '../../utils/apiRequest';

export const getChatArtifacts = async (payload) => {
  try {
    const { page = 1, page_size = 10, data_type, chatId, status } = payload;
    const queryParams = new URLSearchParams({
      page: page.toString(),
      page_size: page_size.toString(),
      data_type: data_type
    });

    if (chatId && chatId !== 'undefined') {
      queryParams.append('chat_id', chatId);
    }
    
    if (status) {
      queryParams.append('status', status);
    }

    const url = `${BACKEND_API_URL.CHAT_DATA_GET_LIST}?${queryParams}`;
    const result = await apiRequest(url, 'GET', payload);
    // Handle new paginated response format
    if (result.results) {
      return {
        data: result.results,
        count: result.count,
        next: result.next,
        previous: result.previous
      };
    }

    // Fallback for old format (backwards compatibility)
    return {
      data: result.data || [],
      count: result.data?.length || 0,
      next: null,
      previous: null
    };
  } catch (error) {
    console.error('Error fetching chat data by chat ID:', error);
    throw error;
  }
};

export const createChatArtifact = async (chatData) => {
  try {
    return await apiRequest(BACKEND_API_URL.CHAT_DATA_CREATE, 'POST', chatData);
  } catch (error) {
    console.error('Error creating chat data:', error);
    throw error;
  }
};

export const updateChatArtifact = async (id, chatData) => {
  try {
    return await apiRequest(BACKEND_API_URL.CHAT_DATA_UPDATE(id), 'PUT', chatData);
  } catch (error) {
    console.error('Error updating chat data:', error);
    throw error;
  }
};

export const deleteChatArtifact = async (id, chatData) => {
  try {
    return await apiRequest(BACKEND_API_URL.CHAT_DATA_DELETE(id), 'DELETE', chatData);
  } catch (error) {
    console.error('Error deleting chat data:', error);
    throw error;
  }
};

export const getChatArtifactCounts = async (payload = {}) => {
  try {
    const result = await apiRequest(BACKEND_API_URL.CHAT_ARTIFACT_COUNTS, 'GET', payload);
    return result || {};
  } catch (error) {
    console.error('Error fetching chat artifact counts:', error);
    throw error;
  }
};


const ChatArtifactService = {
  create: createChatArtifact,
  list: getChatArtifacts,
  update: updateChatArtifact,
  delete: deleteChatArtifact,
  counts: getChatArtifactCounts,
};

export default ChatArtifactService;
