import { BACKEND_API_URL, DB_CONFIG } from '../Constants/constant';
import { IndexedDBUtils } from '../utils/indexedDBUtils';
import { apiRequest } from '../utils/apiRequest';
import { sendMessageToTab } from '../utils/messageSender';

const saveTokenToIndexedDB = async (token) => {
  const stores = [
    { 
      name: DB_CONFIG.STORES.USER_DATA, 
      options: { keyPath: 'id' } 
    }
  ];
  
  const db = await IndexedDBUtils.openDatabase(DB_CONFIG.NAME, DB_CONFIG.VERSION, stores);
  
  // Get existing data to preserve userId and userNumber
  let existingData = null;
  try {
    existingData = await IndexedDBUtils.getFromStore(db, DB_CONFIG.STORES.USER_DATA, 'currentUser');
  } catch (error) {
    console.warn('No existing user data found for token update');
  }
  
  if (!existingData) {
    console.error('Cannot save token without existing user data');
    db.close();
    return;
  }
  
  const userData = {
    ...existingData,
    token,
    timestamp: Date.now()
  };
  
  await IndexedDBUtils.putToStore(db, DB_CONFIG.STORES.USER_DATA, userData);
  db.close();
};

export const GenerateToken = async (userId, tabId) => {
  try {
    const response = await apiRequest(BACKEND_API_URL.GENERATE_TOKEN(userId), 'POST');
    
    if (response?.token) {
      await saveTokenToIndexedDB(response.token);
      
      if (tabId) {
        await sendMessageToTab(tabId, 'GENERATE_TOKEN', { token: response.token });
      }
    }
    
    return response;
  } catch (error) {
    console.error('Error GenerateToken:', error);
    throw new Error(error.message);
  }
};
