import { BaseCheckerService } from './BaseCheckerService.js';
import { getNextOccurrence } from '../../utils/dateUtils';
import chatArtifactsManager from '../../services/ChatArtifacts/chatArtifactsManager';
import { TRACK_EVENTS } from '../../Constants/constant';
import { trackEventFromBackground } from '../trackingHelper';

// Configuration Constants
const CONFIG = {
  ALARM_NAMES: {
    REMINDER_CHECKER: 'reminderChecker',
    REMINDER_SYNC: 'reminderSyncer',
    WINDOW_CLEANUP: 'reminderWindowCleanup'
  },
  INTERVALS: {
    REMINDER_CHECK_MINUTES: 0.5,
    REMINDER_SYNC_MINUTES: 15,
    CLEANUP_MINUTES: 60
  },
  WINDOW: {
    WIDTH: 500,
    HEIGHT: 500,
    DEFAULT_LEFT: 50,
    DEFAULT_TOP: 100
  },
  SYNC: {
    PAGE_SIZE: 100,
    MAX_PAGES: 10,
  },
  SNOOZE: {
    MAX_COUNT: 3,
    DURATION_MINUTES: 15
  }
};

// Application State
class ReminderCheckerState {
  constructor() {
    this.isSetup = false;
    this.openReminderWindows = new Set();
    this.activeSyncProcesses = new Map();
    this.lastSyncTimes = new Map();
  }

  reset() {
    this.isSetup = false;
    this.openReminderWindows.clear();
    this.activeSyncProcesses.clear();
    this.lastSyncTimes.clear();
  }
}

// Utility Functions
class ReminderUtils {
  static createReminderData(reminder, nextDue) {
    const nextDate = nextDue.toISOString().split('T')[0];
    const nextTime = nextDue.toTimeString().split(' ')[0].slice(0, 5);

    return {
      title: reminder.title || 'Untitled',
      description: reminder.description || '',
      date: nextDate,
      time: nextTime,
      recurrence: reminder.recurrence || 0,
      recurrenceType: reminder.recurrenceType || 'day',
      nextDue: nextDue.toISOString()
    };
  }

  static createSnoozeData(reminder) {
    const snoozeTime = new Date();
    snoozeTime.setMinutes(snoozeTime.getMinutes() + CONFIG.SNOOZE.DURATION_MINUTES);

    return {
      title: reminder.title,
      description: reminder.description,
      date: reminder.date,
      time: reminder.time,
      recurrence: reminder.recurrence || 0,
      recurrenceType: reminder.recurrenceType || 'day',
      nextDue: snoozeTime.toISOString(),
      snoozedAt: new Date().toISOString(),
      snoozedCount: (reminder.snoozedCount || 0) + 1
    };
  }
}

// Reminder Checker Service
class ReminderCheckerService extends BaseCheckerService {
  constructor() {
    super(CONFIG);
  }

  createState() {
    return new ReminderCheckerState();
  }

  getServiceName() {
    return 'Reminder Checker';
  }

  getDataType() {
    return 'reminders';
  }

  createAlarms() {
    chrome.alarms.create(CONFIG.ALARM_NAMES.REMINDER_CHECKER, {
      delayInMinutes: CONFIG.INTERVALS.REMINDER_CHECK_MINUTES,
      periodInMinutes: CONFIG.INTERVALS.REMINDER_CHECK_MINUTES
    });

    chrome.alarms.create(CONFIG.ALARM_NAMES.REMINDER_SYNC, {
      delayInMinutes: CONFIG.INTERVALS.REMINDER_SYNC_MINUTES,
      periodInMinutes: CONFIG.INTERVALS.REMINDER_SYNC_MINUTES
    });

    chrome.alarms.create(CONFIG.ALARM_NAMES.WINDOW_CLEANUP, {
      delayInMinutes: CONFIG.INTERVALS.CLEANUP_MINUTES,
      periodInMinutes: CONFIG.INTERVALS.CLEANUP_MINUTES
    });
  }

  handleAlarm(alarm) {
    switch (alarm.name) {
      case CONFIG.ALARM_NAMES.REMINDER_CHECKER:
        console.log('Reminder alarm triggered, checking for due reminders...');
        this.checkForDueReminders();
        break;
      case CONFIG.ALARM_NAMES.REMINDER_SYNC:
        console.log('Reminder sync alarm triggered, syncing reminders...');
        this.syncLoggedUserData();
        break;
      case CONFIG.ALARM_NAMES.WINDOW_CLEANUP:
        console.log('Window cleanup alarm triggered, cleaning up tracking...');
        this.cleanupTracking();
        break;
    }
  }

  async checkForDueReminders() {
    try {
      const userData = await BaseCheckerService.getUserData();
      if (!userData?.userId) {
        console.log('No authenticated user found');
        return;
      }
      await this.checkUserReminders(userData.userId);
    } catch (error) {
      console.error('Error checking for due reminders:', error);
    }
  }

  async checkUserReminders(userId) {
    try {
      const reminders = await this.listAllPendingData(userId);
      const parsedReminders = reminders
        .map(BaseCheckerService.parseRecord)
        .filter(Boolean);

      const dueReminders = parsedReminders.filter(
        reminder => reminder && reminder.nextDue && new Date(reminder.nextDue) <= new Date()
      );

      if (!dueReminders.length) {
        return;
      }

      console.log(`Found ${dueReminders.length} due reminders for user ${userId}`);

      for (const reminder of dueReminders) {
        await this.processReminder(reminder);
      }
    } catch (error) {
      console.error('Error checking user reminders:', error);
    }
  }

  async processReminder(reminder) {
    const key = BaseCheckerService.getItemKey(reminder);

    if (this.state.openReminderWindows.has(key)) {
      console.log(`Skipping reminder ${reminder.title} - window already open`);
      return;
    }

    this.state.openReminderWindows.add(key);

    await this.openReminderWindow(reminder);
  }

  async openReminderWindow(reminder) {
    try {
      const position = await this.calculateWindowPosition();
      const window = await this.createWindow(position);

      await this.storeReminderData(window.id, reminder);
      this.setupWindowCloseHandler(window.id, reminder);
    } catch (error) {
      console.error('Error opening reminder window:', error);
    }
  }

  async calculateWindowPosition() {
    let left = CONFIG.WINDOW.DEFAULT_LEFT;
    let top = CONFIG.WINDOW.DEFAULT_TOP;

    try {
      const currentWindow = await chrome.windows.getCurrent();
      if (currentWindow?.left !== undefined && currentWindow?.width !== undefined) {
        left = Math.max(
          CONFIG.WINDOW.DEFAULT_LEFT,
          currentWindow.left + currentWindow.width - CONFIG.WINDOW.WIDTH - 50
        );
        top = Math.max(CONFIG.WINDOW.DEFAULT_TOP, currentWindow.top + 50);
      }
    } catch (error) {
      // Use default position if we can't get current window info
    }

    return { left, top };
  }

  async createWindow(position) {
    try {
      return await chrome.windows.create({
        url: chrome.runtime.getURL('reminder-window.html'),
        type: 'popup',
        width: CONFIG.WINDOW.WIDTH,
        height: CONFIG.WINDOW.HEIGHT,
        focused: true,
        state: 'normal',
        ...position
      });
    } catch (error) {
      console.error('Error creating reminder window:', error);
      throw error;
    }
  }

  async storeReminderData(windowId, reminder) {
    // Include meta_data in the stored reminder data for the popup window
    let metaData = {};
    if (reminder.meta_data) {
      if (typeof reminder.meta_data === 'string') {
        try {
          metaData = JSON.parse(reminder.meta_data);
        } catch (e) {
          metaData = {};
        }
      } else if (typeof reminder.meta_data === 'object') {
        metaData = reminder.meta_data;
      }
    }

    await chrome.storage.local.set({
      [`reminder_${windowId}`]: {
        ...reminder,
        contactName: metaData.contactName || 'Unknown Contact'
      }
    });
  }

  setupWindowCloseHandler(windowId, reminder) {
    const handleWindowClose = (closedWindowId) => {
      if (closedWindowId === windowId) {
        const key = BaseCheckerService.getItemKey(reminder);
        this.state.openReminderWindows.delete(key);
        chrome.storage.local.remove(`reminder_${windowId}`);
        chrome.windows.onRemoved.removeListener(handleWindowClose);
      }
    };

    chrome.windows.onRemoved.addListener(handleWindowClose);
  }

  async processReminderAction(reminder) {
    try {
      const userId = await BaseCheckerService.getLoggedUserId();
      console.log('Processing reminder:', reminder.title);

      await chatArtifactsManager.update({
        artifact_id: BaseCheckerService.getItemKey(reminder),
        main_content: reminder.main_content,
        meta_data: reminder.meta_data,
        data_type: 'reminders',
        chatId: reminder.chatId || reminder.chat_id,
        loggedUserId: userId,
        status: 'processed'
      });

      console.log(`Marked reminder as processed: ${reminder.title}`);
    } catch (error) {
      console.error('Error processing reminder:', error);
      throw error;
    }
  }

  cleanupTracking() {
    this.state.openReminderWindows.clear();
    console.log('Cleaned up reminder window tracking');
  }
}

// Recurrence Calculator
class RecurrenceCalculator {
  static calculateNextDue(reminder) {
    const currentDue = new Date(reminder.nextDue);
    const step = Number(reminder.recurrence) || 0;
    const currentTime = new Date();

    const calculations = {
      minutes: () => getNextOccurrence(currentDue, step * 60 * 1000, currentTime),
      hour: () => getNextOccurrence(currentDue, step * 60 * 60 * 1000, currentTime),
      day: () => getNextOccurrence(currentDue, step * 24 * 60 * 60 * 1000, currentTime),
      week: () => getNextOccurrence(currentDue, step * 7 * 24 * 60 * 60 * 1000, currentTime),
      month: () => {
        const nextDue = new Date(currentDue);
        while (nextDue <= currentTime) {
          nextDue.setMonth(nextDue.getMonth() + step);
        }
        return nextDue;
      },
      year: () => {
        const nextDue = new Date(currentDue);
        while (nextDue <= currentTime) {
          nextDue.setFullYear(nextDue.getFullYear() + step);
        }
        return nextDue;
      }
    };

    const calculator = calculations[reminder.recurrenceType];
    return calculator ? calculator() : null;
  }

  static async scheduleNextRecurrence(reminder, reminderService) {
    try {
      const nextDue = this.calculateNextDue(reminder);
      if (!nextDue) {
        return; // No recurrence
      }

      const updatedReminderData = ReminderUtils.createReminderData(reminder, nextDue);

      await reminderService.updateData(
        BaseCheckerService.getItemKey(reminder),
        updatedReminderData,
        reminder.chatId || reminder.chat_id,
        'pending',
        reminder.meta_data || {}
      );

      console.log(`Scheduled next recurrence for ${reminder.title} at ${nextDue}`);

    } catch (error) {
      console.error('Error scheduling next recurrence:', error);
      throw error;
    }
  }
}

// Reminder Action Handler
class ReminderActionHandler {
  constructor(checkerService) {
    this.checkerService = checkerService;
  }

  async handleReminderAction(action, reminder) {
    try {
      console.log(`Handling reminder action: ${action} for reminder: ${reminder.title}`);

      const handlers = {
        snooze: () => this.handleSnooze(reminder),
        dismiss: () => this.handleDismiss(reminder)
      };

      const handler = handlers[action];
      if (!handler) {
        throw new Error(`Unknown action: ${action}`);
      }

      await handler();
      return { success: true };
    } catch (error) {
      console.error(`Error handling reminder action ${action}:`, error);
      return { success: false, error: error.message };
    }
  }

  async handleSnooze(reminder) {
    const currentCount = Number(reminder.snoozedCount || 0);

    if (currentCount >= CONFIG.SNOOZE.MAX_COUNT) {
      throw new Error('Snooze limit reached');
    }

    const updatedReminderData = ReminderUtils.createSnoozeData(reminder);
    const snoozeTime = new Date(updatedReminderData.nextDue);

    await this.checkerService.updateData(
      BaseCheckerService.getItemKey(reminder),
      updatedReminderData,
      BaseCheckerService.getChatId(reminder),
      'pending',
      reminder.meta_data || {}
    );

    console.log(`Snoozed reminder ${reminder.title} until ${snoozeTime}`);
    trackEventFromBackground(TRACK_EVENTS.REMINDER_SNOOZED, {
      chatId: getChatId(reminder),
      reminderId: getReminderKey(reminder),
      snoozeCount: updatedReminderData.snoozedCount
    });

    console.log(`Snoozed reminder ${reminder.title} until ${snoozeTime}`);

  }

  async handleDismiss(reminder) {
    const isRecurring = reminder.recurrence && reminder.recurrence > 0;

    if (isRecurring) {
      await RecurrenceCalculator.scheduleNextRecurrence(reminder, this.checkerService);
    } else {
      await this.checkerService.processReminderAction(reminder);
    }

    trackEventFromBackground(TRACK_EVENTS.REMINDER_DISMISSED, {
      chatId: getChatId(reminder),
      reminderId: getReminderKey(reminder),
      isRecurring: isRecurring
    });
  }
}

// Create service instance
const reminderCheckerService = new ReminderCheckerService();
const reminderActionHandler = new ReminderActionHandler(reminderCheckerService);

// Setup function
function setupReminderChecker() {
  reminderCheckerService.setup();
}

// Export the public functions
export {
  setupReminderChecker,
  reminderActionHandler as ReminderActionHandler,
};

// Export handleReminderAction for backward compatibility
export const handleReminderAction = reminderActionHandler.handleReminderAction.bind(reminderActionHandler);