import { BACKEND_API_URL } from '../Constants/constant';
import { apiRequest } from '../utils/apiRequest';

export const fetchQuickReplies = async (payload, tabId) => {
  try {
    return await apiRequest(BACKEND_API_URL.FETCH_QUICK_REPLIES, 'GET', payload, tabId);
  } catch (err) {
    console.error('Error fetching quick replies:', err);
    throw new Error(`${err.message}`);
  }
};

export const saveQuickReplies = async (payload, tabId) => {
  try {
    return await apiRequest(BACKEND_API_URL.SAVE_QUICK_REPLIES, 'PUT', payload, tabId);
  } catch (err) {
    console.error('Error saving quick replies:', err);
    throw new Error(`${err.message}`);
  }
};