import { BaseCheckerService } from './BaseCheckerService.js';
import { getNextOccurrence } from '../../utils/dateUtils';
import chatArtifactsManager from '../../services/ChatArtifacts/chatArtifactsManager';

// Configuration Constants
const CONFIG = {
  ALARM_NAMES: {
    MESSAGE_CHECKER: 'scheduledMessageChecker',
    MESSAGE_SYNC: 'scheduledMessageSyncer',
    MESSAGE_CLEANUP: 'scheduledMessageCleanup'
  },
  INTERVALS: {
    MESSAGE_CHECK_MINUTES: 0.5,
    MESSAGE_SYNC_MINUTES: 15,
    CLEANUP_MINUTES: 60
  },
  SYNC: {
    PAGE_SIZE: 100,
    MAX_PAGES: 10,
  }
};

// Application State
class ScheduledMessageCheckerState {
  constructor() {
    this.isSetup = false;
    this.activeSyncProcesses = new Map();
    this.lastSyncTimes = new Map();
    this.processingMessages = new Set();
  }

  reset() {
    this.isSetup = false;
    this.activeSyncProcesses.clear();
    this.lastSyncTimes.clear();
    this.processingMessages.clear();
  }
}

// Utility Functions
class MessageUtils {

  static createRecurringMessageData(message, nextScheduledTime) {
    const nextDate = nextScheduledTime.toISOString().split('T')[0];
    const nextTime = nextScheduledTime.toTimeString().slice(0, 5);

    return {
      contactName: message.contactName,
      date: nextDate,
      time: nextTime,
      nextDue: nextScheduledTime.toISOString(),
      recurrence: message.recurrence || message.meta_data?.recurrence || 0,
      recurrenceType: message.recurrenceType || message.meta_data?.recurrenceType || 'day'
    };
  }

  static parseMessage(record) {
    if (!record) return null;

    try {
      // main_content now contains only the message text
      const messageText = record.main_content || '';

      // meta_data is JSONB containing all other fields
      let metaData = {};
      if (record.meta_data) {
        if (typeof record.meta_data === 'string') {
          metaData = JSON.parse(record.meta_data);
        } else if (typeof record.meta_data === 'object') {
          metaData = record.meta_data;
        }
      }

      return {
        id: record.id,
        apiId: record.id,
        message: messageText,
        // Use nextDue for scheduled time (Reminders.js pattern)
        nextDue: metaData.nextDue,
        date: metaData.date,
        time: metaData.time,
        recurrence: metaData.recurrence || 0,
        recurrenceType: metaData.recurrenceType || 'day',
        contactName: metaData.contactName,
        status: record.status || 'pending',
        chatId: record.chat_id,
        chat_id: record.chat_id,
        sentAt: metaData.sentAt,
        failureReason: metaData.failureReason,
        main_content: messageText,
        meta_data: metaData
      };
    } catch (error) {
      console.error('Error parsing message record:', error);
      return null;
    }
  }
}

// Scheduled Message Checker Service
class ScheduledMessageCheckerService extends BaseCheckerService {
  constructor() {
    super(CONFIG);
  }

  createState() {
    return new ScheduledMessageCheckerState();
  }

  getServiceName() {
    return 'Scheduled Message Checker';
  }

  getDataType() {
    return 'scheduled_messages';
  }

  createAlarms() {
    chrome.alarms.create(CONFIG.ALARM_NAMES.MESSAGE_CHECKER, {
      delayInMinutes: CONFIG.INTERVALS.MESSAGE_CHECK_MINUTES,
      periodInMinutes: CONFIG.INTERVALS.MESSAGE_CHECK_MINUTES
    });

    chrome.alarms.create(CONFIG.ALARM_NAMES.MESSAGE_SYNC, {
      delayInMinutes: CONFIG.INTERVALS.MESSAGE_SYNC_MINUTES,
      periodInMinutes: CONFIG.INTERVALS.MESSAGE_SYNC_MINUTES
    });

    chrome.alarms.create(CONFIG.ALARM_NAMES.MESSAGE_CLEANUP, {
      delayInMinutes: CONFIG.INTERVALS.CLEANUP_MINUTES,
      periodInMinutes: CONFIG.INTERVALS.CLEANUP_MINUTES
    });
  }

  handleAlarm(alarm) {
    switch (alarm.name) {
      case CONFIG.ALARM_NAMES.MESSAGE_CHECKER:
        console.log('Scheduled message alarm triggered, checking for due messages...');
        this.checkForDueMessages();
        break;
      case CONFIG.ALARM_NAMES.MESSAGE_SYNC:
        console.log('Scheduled message sync alarm triggered, syncing messages...');
        this.syncLoggedUserData();
        break;
      case CONFIG.ALARM_NAMES.MESSAGE_CLEANUP:
        console.log('Scheduled message cleanup alarm triggered, cleaning up tracking...');
        this.cleanupTracking();
        break;
    }
  }

  async checkForDueMessages() {
    try {
      const userData = await BaseCheckerService.getUserData();
      if (!userData?.userId) {
        console.log('No authenticated user found');
        return;
      }
      await this.checkUserMessages(userData.userId);
    } catch (error) {
      console.error('Error checking for due scheduled messages:', error);
    }
  }

  async checkUserMessages(userId) {
    try {
      const messages = await this.listAllPendingData(userId);
      const parsedMessages = messages
        .map(MessageUtils.parseMessage)
        .filter(Boolean);

      const currentTime = new Date();
      const dueMessages = parsedMessages.filter(
        message => message && message.nextDue && new Date(message.nextDue) <= currentTime
      );

      if (!dueMessages.length) {
        return;
      }

      for (const message of dueMessages) {
        // Check if message is overdue by more than 5 minutes
        const messageTime = new Date(message.nextDue);
        const timeDiffMinutes = (currentTime - messageTime) / (1000 * 60);

        if (timeDiffMinutes > 5) {
          // Message is too old (>5 minutes overdue), skip sending
          console.log(`[${new Date()}][INFO] Skipping overdue message (${timeDiffMinutes.toFixed(1)} minutes late): ${BaseCheckerService.getItemKey(message)}`);
          await this.handleMessageFailure(message, `Message skipped - overdue`);
        } else {
          // Message is within acceptable time window, process normally
          await this.processMessage(message);
        }
      }
    } catch (error) {
      console.error('Error checking user scheduled messages:', error);
    }
  }

  async processMessage(message) {
    const key = BaseCheckerService.getItemKey(message);

    if (this.state.processingMessages.has(key)) {
      console.log(`Skipping scheduled message ${message.message} - already processing`);
      return;
    }

    this.state.processingMessages.add(key);

    try {
      await this.deliverMessage(message);
    } catch (error) {
      console.error(`Error processing scheduled message ${key}:`, error);
      // Handle failure - check if recurring
      await this.handleMessageFailure(message, error.message);
    } finally {
      this.state.processingMessages.delete(key);
    }
  }

  async deliverMessage(message) {
    try {
      console.log(`[${new Date()}][INFO] Delivering scheduled message: ${BaseCheckerService.getItemKey(message)}`);

      // Send message to content script for delivery to WhatsApp Web
      const tabs = await chrome.tabs.query({ url: '*://web.whatsapp.com/*' });

      if (tabs.length === 0) {
        throw new Error('WhatsApp Web is not open');
      }

      // Prepare message payload
      const payload = {
        messageId: BaseCheckerService.getItemKey(message),
        message: message.message,
        chatId: BaseCheckerService.getChatId(message),
        sentAt: new Date().toISOString()
      };

      // Send the message delivery request to content script
      let delivered = false;
      for (const tab of tabs) {
        try {
          await chrome.tabs.sendMessage(tab.id, {
            type: 'DELIVER_SCHEDULED_MESSAGE',
            payload: payload
          });

          console.log(`[${new Date()}][INFO] Scheduled message delivery request sent for: ${BaseCheckerService.getItemKey(message)}`);
          delivered = true;
          break; // Success, no need to try other tabs
        } catch (tabError) {
          console.warn(`Failed to send to tab ${tab.id}:`, tabError);
        }
      }

      if (!delivered) {
        throw new Error('Failed to deliver message');
      }

      // Note: Message delivery success/failure is handled by MessageActionHandler
      // when the WhatsApp tab sends back a response (SCHEDULED_MESSAGE_DELIVERY_SUCCESS/FAILURE)
      console.log(`[${new Date()}][INFO] Scheduled message delivery request completed for: ${BaseCheckerService.getItemKey(message)}`);

    } catch (error) {
      console.error(`Failed to deliver scheduled message ${BaseCheckerService.getItemKey(message)}:`, error);
      throw error;
    }
  }

  async handleMessageDelivered(message, sentAt) {
    try {
      console.log(`[${new Date()}][INFO] Handling successful delivery for scheduled message:`, BaseCheckerService.getItemKey(message));
      // Check if message has recurrence enabled using Reminders.js structure
      const metaData = message.meta_data || {};
      const recurrence = metaData.recurrence || 0;
      const isRecurring = recurrence > 0;

      if (isRecurring) {
        // For recurring messages, schedule the next occurrence
        await RecurrenceCalculator.scheduleNextMessage(message, this, {
          deliveryStatus: 'sent',
          lastSentAt: sentAt
        });
      } else {
        // For non-recurring messages, mark as processed with delivery status in metadata
        await this.updateMessageStatus(message, 'processed', {
          deliveryStatus: 'sent',
          sentAt
        });
      }
    } catch (error) {
      console.error('Error handling message delivery:', error);
    }
  }

  async handleMessageFailure(message, failureReason) {
    try {
      // Check if message has recurrence enabled
      const metaData = message.meta_data || {};
      const recurrence = metaData.recurrence || 0;
      const isRecurring = recurrence > 0;

      if (isRecurring) {
        // For recurring messages, schedule the next occurrence despite failure
        console.log(`[${new Date()}][INFO] Recurring message failed, scheduling next occurrence:`, BaseCheckerService.getItemKey(message));
        await RecurrenceCalculator.scheduleNextMessage(message, this, {
          deliveryStatus: 'failed',
          lastFailureReason: failureReason,
          lastFailedAt: new Date().toISOString()
        });
      } else {
        // For non-recurring messages, mark as processed with failure status in metadata
        await this.updateMessageStatus(message, 'processed', {
          deliveryStatus: 'failed',
          failureReason,
          failedAt: new Date().toISOString()
        });
      }
    } catch (error) {
      console.error('Error handling message failure:', error);
    }
  }

  async updateMessageStatus(message, status, additionalMetaData = {}) {
    try {
      const userId = await BaseCheckerService.getLoggedUserId();
      const messageKey = BaseCheckerService.getItemKey(message);
      const chatId = BaseCheckerService.getChatId(message);

      // Fetch the original message data to preserve content
      const originalMessage = await this.getOriginalMessageData(messageKey, chatId, userId);

      if (!originalMessage) {
        throw new Error(`Original message data not found for message ${messageKey}`);
      }

      // main_content contains only the message text (no parsing needed)
      const messageText = originalMessage.main_content || '';

      // Parse meta_data which contains all other fields
      let originalMetaData = {};
      if (originalMessage.meta_data) {
        if (typeof originalMessage.meta_data === 'string') {
          try {
            originalMetaData = JSON.parse(originalMessage.meta_data);
          } catch (parseError) {
            console.warn('Failed to parse original meta_data, using empty object:', parseError);
          }
        } else if (typeof originalMessage.meta_data === 'object') {
          originalMetaData = originalMessage.meta_data;
        }
      }

      // Update meta_data with status and additional information
      const updatedMetaData = {
        ...originalMetaData,
        ...additionalMetaData,
        status
      };

      await chatArtifactsManager.update({
        artifact_id: messageKey,
        main_content: messageText,
        meta_data: updatedMetaData,
        data_type: 'scheduled_messages',
        chatId,
        loggedUserId: userId,
        status
      });

      console.log(`[${new Date()}][INFO] Updated scheduled message status to ${status}:`, messageKey);
    } catch (error) {
      console.error(`Error updating scheduled message status to ${status}:`, error);
      throw error;
    }
  }

  async getOriginalMessageData(messageId, chatId, userId) {
    try {
      // Fetch the message data from the API using chatArtifactsManager
      const messages = await chatArtifactsManager.list({
        chatId: chatId,
        loggedUserId: userId,
        data_type: 'scheduled_messages',
        pageSize: 100 // Should be enough to find a specific message
      });

      // Find the message with matching ID
      const originalMessage = messages?.data?.find(msg =>
        (msg.id && msg.id.toString() === messageId.toString()) ||
        (msg.apiId && msg.apiId.toString() === messageId.toString())
      );

      return originalMessage;
    } catch (error) {
      console.error('Error fetching original message data:', error);
      return null;
    }
  }

  cleanupTracking() {
    this.state.processingMessages.clear();
    console.log('Cleaned up scheduled message processing tracking');
  }

  async updateData(messageId, messageData, chatId, status, metaData) {
    try {
      const userId = await BaseCheckerService.getLoggedUserId();

      await chatArtifactsManager.update({
        artifact_id: messageId,
        main_content: messageData.message || '',
        meta_data: metaData,
        data_type: 'scheduled_messages',
        chatId: chatId,
        loggedUserId: userId,
        status: status
      });
    } catch (error) {
      console.error('Error updating message data:', error);
      throw error;
    }
  }
}

// Recurrence Calculator
class RecurrenceCalculator {
  static calculateNextScheduledTime(message) {
    const metaData = message.meta_data || {};
    const recurrence = metaData.recurrence || 0;
    const recurrenceType = metaData.recurrenceType || 'day';

    if (!recurrence || recurrence === 0) {
      return null;
    }

    const currentScheduledTime = new Date(message.nextDue || metaData.nextDue);
    const step = Number(recurrence) || 1;
    const currentTime = new Date();

    const calculations = {
      minutes: () => getNextOccurrence(currentScheduledTime, step * 60 * 1000, currentTime),
      hour: () => getNextOccurrence(currentScheduledTime, step * 60 * 60 * 1000, currentTime),
      day: () => getNextOccurrence(currentScheduledTime, step * 24 * 60 * 60 * 1000, currentTime),
      week: () => getNextOccurrence(currentScheduledTime, step * 7 * 24 * 60 * 60 * 1000, currentTime),
      month: () => {
        const nextScheduled = new Date(currentScheduledTime);
        while (nextScheduled <= currentTime) {
          nextScheduled.setMonth(nextScheduled.getMonth() + step);
        }
        return nextScheduled;
      },
      year: () => {
        const nextScheduled = new Date(currentScheduledTime);
        while (nextScheduled <= currentTime) {
          nextScheduled.setFullYear(nextScheduled.getFullYear() + step);
        }
        return nextScheduled;
      }
    };

    const calculator = calculations[recurrenceType];
    return calculator ? calculator() : null;
  }

  static async scheduleNextMessage(message, messageService, additionalMetaData = {}) {
    try {
      const nextScheduledTime = this.calculateNextScheduledTime(message);
      if (!nextScheduledTime) {
        console.log('No next occurrence calculated for message');
        return;
      }

      // Create updated message data with new scheduled time
      const updatedMessageData = {
        message: message.message || message.main_content || '',
        nextDue: nextScheduledTime.toISOString()
      };

      // Get original meta_data and update it
      let originalMetaData = {};
      if (message.meta_data) {
        if (typeof message.meta_data === 'string') {
          try {
            originalMetaData = JSON.parse(message.meta_data);
          } catch (e) {
            originalMetaData = {};
          }
        } else if (typeof message.meta_data === 'object') {
          originalMetaData = message.meta_data;
        }
      }

      const updatedMetaData = {
        ...originalMetaData,
        date: nextScheduledTime.toISOString().split('T')[0],
        time: nextScheduledTime.toTimeString().slice(0, 5),
        nextDue: nextScheduledTime.toISOString(),
        lastProcessedAt: new Date().toISOString(),
        ...additionalMetaData
      };

      await messageService.updateData(
        BaseCheckerService.getItemKey(message),
        updatedMessageData,
        message.chatId || message.chat_id,
        'pending',
        updatedMetaData
      );

      console.log(`Scheduled next message occurrence at ${nextScheduledTime}`);

    } catch (error) {
      console.error('Error scheduling next message occurrence:', error);
      throw error;
    }
  }
}

// Message Action Handler
class MessageActionHandler {
  constructor(checkerService) {
    this.checkerService = checkerService;
  }

  async handleMessageDeliverySuccess(payload) {
    try {
      const { messageId, sentAt, chatId } = payload;
      console.log(`Processing successful delivery for message ${messageId} at ${sentAt}`);

      // Find the message to update
      const userData = await BaseCheckerService.getUserData();
      if (!userData?.userId) {
        throw new Error('No authenticated user found');
      }

      // Fetch the original message data to get recurrence info
      const originalMessage = await this.checkerService.getOriginalMessageData(messageId, chatId, userData.userId);

      if (!originalMessage) {
        throw new Error(`Original message data not found for message ${messageId}`);
      }

      // Parse the meta_data for recurrence info
      let metaData = {};
      if (originalMessage.meta_data) {
        if (typeof originalMessage.meta_data === 'string') {
          try {
            metaData = JSON.parse(originalMessage.meta_data);
          } catch (e) {
            metaData = {};
          }
        } else if (typeof originalMessage.meta_data === 'object') {
          metaData = originalMessage.meta_data;
        }
      }

      // Create a message object for processing with full data
      const message = {
        id: messageId,
        apiId: messageId,
        chatId: chatId,
        chat_id: chatId,
        message: originalMessage.main_content || '',
        main_content: originalMessage.main_content || '',
        nextDue: metaData.nextDue,
        recurrence: metaData.recurrence,
        meta_data: metaData
      };

      await this.checkerService.handleMessageDelivered(message, sentAt);

      console.log(`Successfully processed delivery success for message ${messageId}`);
      return { success: true };
    } catch (error) {
      console.error(`Error handling message delivery success for ${payload?.messageId}:`, error);
      return { success: false, error: error.message };
    }
  }

  async handleMessageDeliveryFailure(payload) {
    try {
      const { messageId, failureReason, chatId } = payload;
      console.log(`Handling failed delivery for message ${messageId}: ${failureReason}`);

      // Find the message to update
      const userData = await BaseCheckerService.getUserData();
      if (!userData?.userId) {
        throw new Error('No authenticated user found');
      }

      // Fetch the original message data to get recurrence info
      const originalMessage = await this.checkerService.getOriginalMessageData(messageId, chatId, userData.userId);

      if (!originalMessage) {
        throw new Error(`Original message data not found for message ${messageId}`);
      }

      // Parse the meta_data for recurrence info
      let metaData = {};
      if (originalMessage.meta_data) {
        if (typeof originalMessage.meta_data === 'string') {
          try {
            metaData = JSON.parse(originalMessage.meta_data);
          } catch (e) {
            metaData = {};
          }
        } else if (typeof originalMessage.meta_data === 'object') {
          metaData = originalMessage.meta_data;
        }
      }

      // Create a message object for processing with full data
      const message = {
        id: messageId,
        apiId: messageId,
        chatId: chatId,
        chat_id: chatId,
        message: originalMessage.main_content || '',
        main_content: originalMessage.main_content || '',
        nextDue: metaData.nextDue,
        recurrence: metaData.recurrence,
        meta_data: metaData
      };

      await this.checkerService.handleMessageFailure(message, failureReason);
      return { success: true };
    } catch (error) {
      console.error(`Error handling message delivery failure:`, error);
      return { success: false, error: error.message };
    }
  }
}

// Create service instance
const scheduledMessageCheckerService = new ScheduledMessageCheckerService();
const messageActionHandler = new MessageActionHandler(scheduledMessageCheckerService);

// Setup function
function setupScheduledMessageChecker() {
  scheduledMessageCheckerService.setup();
}

// Export the public functions
export {
  setupScheduledMessageChecker,
  messageActionHandler as MessageActionHandler,
};

// Export handleMessageAction for backward compatibility
export const handleScheduledMessageDeliverySuccess = messageActionHandler.handleMessageDeliverySuccess.bind(messageActionHandler);
export const handleScheduledMessageDeliveryFailure = messageActionHandler.handleMessageDeliveryFailure.bind(messageActionHandler);