import CustomerService from "./CustomerService";
import { sendMessageToWhatsAppTabs } from "../../utils/sendMessageToWhatsappTab";
import { BACKEND_API_URL } from "../../Constants/constant";
import { apiRequest } from "../../utils/apiRequest";

class CustomerManager {
    
    async create(payload) {
        try {
            // Map new API payload; provide fallback from legacy fields if present
            let derivedCompanyId = payload.company_id;
            let derivedRelationship = payload.relationship_type;
            if ((derivedCompanyId === undefined || derivedCompanyId === null) && Array.isArray(payload.companies) && payload.companies.length > 0) {
                const firstCompany = payload.companies.find(c => c && (c.id !== undefined && c.id !== null));
                if (firstCompany) {
                    derivedCompanyId = firstCompany.id;
                    derivedRelationship = firstCompany.relationship_type;
                }
            }

            const customerData = {
                chat_id: payload.chat_id,
                phone: payload.phone,
                email: payload.email,
                first_name: payload.first_name,
                last_name: payload.last_name,
                city: payload.city,
                state: payload.state,
                country: payload.country,
                street_address: payload.street_address,
                source: payload.source,
                sync_to_phone: payload.sync_to_phone,
                company_id: derivedCompanyId,
                relationship_type: derivedRelationship
            };

            const result = await CustomerService.create(customerData);
            
            this.pushToFrontend('CUSTOMER_CREATED', result);
            
            return result;
        } catch (error) {
            console.error('Error in CustomerManager create method:', error);
            return { success: false, error: error.message };
        }
    }

    async update(payload) {
        
        try {
            const customerId = payload.customer_id;
            
            if (!customerId || customerId === 'undefined') {
                return { success: false, error: 'Invalid customer ID' };
            }
            
            const customerData = {};

            // Only include fields that are provided in the payload
            if (payload.email !== undefined) customerData.email = payload.email;
            if (payload.first_name !== undefined) customerData.first_name = payload.first_name;
            if (payload.last_name !== undefined) customerData.last_name = payload.last_name;
            if (payload.phone !== undefined) customerData.phone = payload.phone;
            if (payload.city !== undefined) customerData.city = payload.city;
            if (payload.state !== undefined) customerData.state = payload.state;
            if (payload.country !== undefined) customerData.country = payload.country;
            if (payload.street_address !== undefined) customerData.street_address = payload.street_address;
            if (payload.source !== undefined) customerData.source = payload.source;
            if (payload.sync_to_phone !== undefined) customerData.sync_to_phone = payload.sync_to_phone;
            // Derive company from new fields or legacy ones
            let updateCompanyId = payload.company_id;
            let updateRelationship = payload.relationship_type;
            if ((updateCompanyId === undefined || updateCompanyId === null) && Array.isArray(payload.companies_to_add_or_update) && payload.companies_to_add_or_update.length > 0) {
                const firstCompany = payload.companies_to_add_or_update.find(c => c && (c.id !== undefined && c.id !== null));
                if (firstCompany) {
                    updateCompanyId = firstCompany.id;
                    updateRelationship = firstCompany.relationship_type;
                }
            }
            if (updateCompanyId !== undefined) customerData.company_id = updateCompanyId;
            if (updateRelationship !== undefined) customerData.relationship_type = updateRelationship;

            const result = await CustomerService.update(customerId, customerData);
            
            this.pushToFrontend('CUSTOMER_UPDATED', result);
            
            return result;
        } catch (error) {
            console.error('Error in CustomerManager update method:', error);
            return { success: false, error: error.message };
        }
    }

    async get(payload) {
        try {
            const chatId = payload.chat_id;
            const result = await CustomerService.get(chatId);
            
            this.pushToFrontend('CUSTOMER_RETRIEVED', result);
            
            return result;
        } catch (error) {
            console.error('Error in CustomerManager get method:', error);
            return { success: false, error: error.message };
        }
    }

    async delete(payload) {
        try {
            const customerId = payload.customer_id;
            if (!customerId || customerId === 'undefined') {
                return { success: false, error: 'Invalid customer ID' };
            }

            const result = await CustomerService.delete(customerId);

            this.pushToFrontend('CUSTOMER_DELETED', result);

            return result;
        } catch (error) {
            console.error('Error in CustomerManager delete method:', error);
            return { success: false, error: error.message };
        }
    }

    async getCompanies(payload) {
        try {
            const result = await CustomerService.getCompanies(payload);
            
            this.pushCompaniesToFrontend('COMPANIES_RETRIEVED', result);
            
            return result;
        } catch (error) {
            console.error('Error in CustomerManager getCompanies method:', error);
            return { success: false, error: error.message };
        }
    }

    // Create a new company
    async createCompany(payload) {
        try {
            const companyData = {
                name: payload.name,
                domain: payload.domain,
                industry: payload.industry,
            };

            // Include requestType so apiRequest can suppress Sentry for 400s
            const result = await apiRequest(BACKEND_API_URL.COMPANY_CREATE, 'POST', { ...companyData, requestType: payload.requestType });

            this.pushCompaniesToFrontend('COMPANY_CREATED', result);
            return result;
        } catch (error) {
            console.error('Error in CustomerManager createCompany method:', error);
            return { success: false, error: error.message };
        }
    }

    // Update an existing company
    async updateCompany(payload) {
        try {
            const companyId = payload.company_id;
            if (!companyId && companyId !== 0) {
                return { success: false, error: 'Invalid company ID' };
            }

            const companyData = {};
            if (payload.name !== undefined) companyData.name = payload.name;
            if (payload.domain !== undefined) companyData.domain = payload.domain;
            if (payload.industry !== undefined) companyData.industry = payload.industry;

            // Include requestType so apiRequest can suppress Sentry for 400s
            const result = await apiRequest(BACKEND_API_URL.COMPANY_UPDATE(companyId), 'PATCH', { ...companyData, requestType: payload.requestType });

            this.pushCompaniesToFrontend('COMPANY_UPDATED', result);
            return result;
        } catch (error) {
            console.error('Error in CustomerManager updateCompany method:', error);
            return { success: false, error: error.message };
        }
    }

    pushToFrontend(actionType, payload) {
        sendMessageToWhatsAppTabs({
            type: 'CUSTOMER_UPDATE',
            actionType,
            data: payload
        });
    }

    pushCompaniesToFrontend(actionType, payload) {
        sendMessageToWhatsAppTabs({
            type: 'COMPANIES_UPDATE',
            actionType,
            data: payload
        });
    }
}

const customerManager = new CustomerManager();
export default customerManager;
